
.gallery {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(20rem, 1fr));
    grid-gap: 1rem;
    max-width: 80rem;
    margin-left: 200px;
/*    margin: 5rem auto;*/
    /*padding: 0 5rem;*/
}
.gallery-panel img {
    width: 100%;
    height: 22vw;
    -o-object-fit: cover;
       object-fit: cover;
    border-radius: 0.75rem;
}

.menu {
  width: 200px;
  position: fixed;
  border-style: solid;
}

.lightbox {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 2rem;
}
.lightbox img {
  margin: auto;
  width: 100%;
  grid-column-start: 2;
}
.lightbox-info {
  margin: auto 2rem auto 0;
}
.lightbox-info-inner {
  background-color: #FFFFFF;
  display: inline-block;
  padding: 2rem;
}
